.carousel{
    max-width: 100%;
    margin:auto;
    // box-shadow: 5px 5px 5px 1px rgba(0, 0, 0, 0.2);
    border-radius:10px;
    width:100%;
    max-height:300px;
    height:100%;
}

.carousel-inner{
    border-radius:10px;
    height:100%;
}

.carousel-item{
    min-height:300px;
    max-height:300px;
}

.d-block{
    display:block;
    height:auto;
    max-height:100%;
    left:50%;
    position:absolute;
    top:50%;
    transform:translate(-50%,-50%);
    width:auto;
    min-width:100%;
}
