.card {
  box-shadow: 7px 7px 5px 1.25px rgba(0, 0, 0, 0.25);
  border-radius: 0.25rem;
  max-width: 500px !important;
  min-width: 500px !important;
  margin-bottom: 30px;

  &.team-card{
    max-width: 300px !important;
    min-width: 300px !important;
  }

  .card-body {
    padding-top: 0 !important;

    .card-title {
      text-align: left;
      color: map-get($colors, _blue);
    }
    .card-text {
      text-align: left;
      color: map-get($colors, _blue);
      font-weight: 600;
      margin-bottom: 0.5rem !important;
      font-size: 0.9em;

      &.sizes {
        margin-bottom: 0.15rem !important;
      }

      &.height {
        margin-left: 65px;
      }

      &.price {
        color: map-get($colors, _red);
        font-weight: bold;
        font-size: 2em;
      }
    }

    .card-info-container {
      justify-content: space-around;
    }
  }
}
